<template>
    <div class="city_container">
        <header>
            <i class="el-icon-arrow-left" @click="goBack"></i>
            <section class="head_title">
                <span>{{titleText}}</span>
            </section>
            <router-link to="/home">切换城市</router-link>
        </header>
        <div class="city_form">
            <div>
                <el-input placeholder="输入学校、商务楼、地址" v-model="inputV" clearable class="city_input"></el-input>
            </div>
            <div>
                <el-button type="primary" class="city_btn" @click="submitV">提交</el-button>
            </div>
        </div>
        <div class="search_history" v-if="isShow_h">搜索历史</div>
        <ul class="getpois_ul">
            <li v-for="(item, index) in details" :key="item.geohash" @click="addLocal(item)">
                <h4>{{item.name}}</h4>
                <p class="ellipsis">{{item.address}}</p>
            </li>
        </ul>
        <div class="none_place" v-if="isShow_n">很抱歉！无搜索结果</div>
        <footer class="clear_all" @click="clearAll" v-if="isShow_c">清空所有</footer>
    </div>
</template>

<script>
    export default {
        name: "City",
        data(){
            return {
                titleText:"",
                inputV:"",
                details:[],
                isShow_h:true,
                isShow_c:false,
                isShow_n:false
            }
        },
        created(){
            this.axios.get("/v1/cities/"+this.$route.params.cityId).then(res=>{
                this.titleText = res.name;
                if(localStorage.placeHistory != undefined){
                    this.isShow_c = true;
                    this.details = JSON.parse(localStorage.getItem("placeHistory"));
                }
            }).catch(err=>{});
        },
        methods:{
            goBack(){
                this.$router.go(-1);
            },
            submitV(){
                if(this.inputV.trim().length){
                this.axios.get("/v1/pois",{
                    params:{city_id:this.$route.params.cityId, keyword:this.inputV}
                }).then(res=>{
                        this.isShow_h = false;
                        if(res.length){
                            this.details = res;
                        }else {
                            this.isShow_n = true;
                        }
                }).catch(err=>{});
            }
            },
            addLocal(address){
                if(!localStorage.placeHistory){
                    let arr = [address];
                    localStorage.setItem("placeHistory",JSON.stringify(arr));
                }else {
                    let newArr = JSON.parse(localStorage.getItem("placeHistory"));
                    let filterArr = newArr.filter(v=>{
                        return v.name == address.name;
                    });
                    if(!filterArr.length){
                        newArr.push(address);
                        localStorage.setItem("placeHistory",JSON.stringify(newArr))
                    }
                }
                localStorage.setItem("geohash",address.geohash);
                this.$router.push({path:"/msite"}).catch(err=>{});
            },
            clearAll(){
                this.isShow_c = false;
                localStorage.removeItem("placeHistory");
                this.details.splice(0, this.details.length);
            }
        }
    }
</script>

<style scoped>
    .city_container {
        padding-top: 2.35rem;
    }
    header>i {
        font-size: 1rem;
        line-height: 2rem;
        margin-left: .4rem;
    }
    .head_title {
        width: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        text-align: center;
        transform: translate(-50%,-50%);
    }
    .head_title>span {
        font-size: .8rem;
        font-weight: 700;
    }
    header>a {
        position: absolute;
        top: 50%;
        right: .4rem;
        transform: translateY(-50%);
        font-size: .6rem;
        color: #fff;
    }
    .city_form {
        background-color: #fff;
        border-top: 1px solid #e4e4e4;
        border-bottom: 1px solid #e4e4e4;
        padding-top: .4rem;
    }
    .city_form>div:nth-child(1) {
        width: 90%;
        margin: 0 auto;
        text-align: center;
    }
    .city_input {
        width: 100%;
        margin-bottom: .4rem;
        padding: 0 .3rem;
        font-size: .65rem;
        color: #333;
        box-sizing: border-box;
        outline: none;
    }
    .city_form>div:nth-child(2) {
        width: 90%;
        margin: 0 auto;
        text-align: center;
    }
    .city_btn {
        font-size: .65rem;
        width: 100%;
        margin-bottom: .4rem;
    }
    .search_history {
        border-top: 1px solid #e4e4e4;
        border-bottom: 1px solid #e4e4e4;
        padding-left: .5rem;
        font: .475rem/.8rem Microsoft YaHei;
    }
    .getpois_ul {
        background-color: #fff;
        border-top: 1px solid #e4e4e4;
    }
    .getpois_ul>li {
        padding-top: .65rem;
        border-bottom: 1px solid #e4e4e4;
    }
    .getpois_ul h4 {
        margin: 0 auto .35rem;
        width: 90%;
        font-size: .65rem;
        color: #333;
    }
    .getpois_ul p {
        width: 90%;
        margin: 0 auto .55rem;
        font-size: .45rem;
        color: #999;
    }
    .none_place {
        width: 100%;
        font: .65rem/1.75rem Microsoft YaHei;
        color: #333;
        background-color: #fff;
        text-indent: .5rem;
    }
    footer {
        font-size: .7rem;
        color: #666;
        text-align: center;
        line-height: 2rem;
        background-color: #fff;
    }
</style>